.var-blue{
	color: blue;
}
.var-yellow{
	color: pink;
}
.var-lightblue{
  color: lightblue;
}
.var-purple{
	color: purple;
}
.var-black{
	color: black;
}
.string-highlight{
	color: rgba(253, 149, 90, 0.8);
}

#typewriter{
		font-size: 2em;
		margin: 0;
		font-family: "Courier New";
/* 
		&:after{
			content: "|";
			animation: blink 500ms linear infinite alternate;
		} */
}

@-webkit-keyframes blink{
	0%{opacity: 0;}
	100%{opacity: 1;}
}

@-moz-keyframes blink{
	0%{opacity: 0;}
	100%{opacity: 1;}
}

@keyframes blink{
	0%{opacity: 0;}
	100%{opacity: 1;}
}